.contact-form a{
    color:white;
    text-decoration: none;
    border: solid 1px black;
    padding:8px;
    border-radius: 20px;
    font-size: 18px;
    
}

.contact-form a:hover{
    border-color:white;
    background: rgb(2,0,36);
    background: linear-gradient(41deg, rgba(2,0,36,1) 0%, rgba(0,172,255,1) 84%);
    color:black;
    font-weight: bolder;
    text-shadow: white 1px 2px ;
}

.contact-form h2{
    text-align :center;
}

/* Footer Section */
.follow{
    background-color: black;;
}

.flink:hover{
    transform:scale(1.1,1.1);
    transition: transform 0.2s ease-in;
}

.Footer-Container{
    position: fixed;
    left: 0;
    bottom: 0;
    width: 100%;
}